import React from "react";
import "../styles/servicehomemobile.scss";
import web from "../images/stock4.jpg";
import sale from "../images/sale.jpg";
import digital from "../images/digital-marketing.jpg";
import negociation from "../images/nego.jpg";

const Animatemobile = () => {
  return (
    <div className="container-fluid servicehomemobile">
      <div className="row serrowmobile">
        <div className="col-md-6 img-text">
          <img src={web} alt="" height="auto" width="100%" />
        </div>
        <div className="col-md-6 text">
          <h1
            className="webmobile-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            PRODUCT SOURCING
          </h1>
          <p
            className="webmobile-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are working with a list of reliable suppliers and even if we do
            not know a manufacturer of the products you are looking for, it is
            easy for us to find reliable and suitable ones.
          </p>
          <div
            className="div-moremobile grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="moremobile">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>

      <div className="row serrowmobile">
        <div className="col-md-6 img-text">
          <img src={negociation} alt="" height="auto" width="100%" />
        </div>
        <div className="col-md-6 text">
          <h1
            className="importmobile-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            IMPORT FROM TURKEY
          </h1>
          <p
            className="importmobile-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We assist small business importers find potential suppliers from
            Turkey. We source best products, facilitate negotiation, find
            competitive prices, and take care of documentation. We also follow
            up production and delivery.
          </p>
          <div
            className="div-moremobile grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="moremobile">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>

      <div className="row serrowmobile">
        <div className="col-md-6 img-text">
          <img src={sale} alt="" height="auto" width="100%" />
        </div>
        <div className="col-md-6 text">
          <h1
            className="exportmobile-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            EXPORT TO AFRICA
          </h1>
          <p
            className="exportmobile-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are sourcing agents, facilitating good business between Turkish
            manufacturers and African customers. We help manufacturers find
            potential clients and sell their products in the African market.
          </p>
          <div
            className="div-moremobile grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="moremobile">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>

      <div className="row serrowmobile">
        <div className="col-md-6 img-text">
          <img src={digital} alt="" height="auto" width="100%" />
        </div>
        <div className="col-md-6 text">
          <h1
            className="digitalmobile-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            DIGITAL MARKETING
          </h1>
          <p
            className="digitalmobile-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are also digital marketers, using any form of digital channel to
            promote products or services, or to connect with the clients.
          </p>
          <div
            className="div-moremobile grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="moremobile">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Animatemobile;
