import React from "react";
import "../styles/servicehome.scss";
import web from "../images/stock4.jpg";
import sale from "../images/sale.jpg";
import digital from "../images/digital-marketing.jpg";
import negociation from "../images/nego.jpg";

const Animate = () => {
  return (
    <div className="container-fluid servicehome">
      <div className="row serrow">
        <div className="col-md-6 text-col">
          <h1
            className="web-h1wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            PRODUCT SOURCING
          </h1>
          <p
            className="web-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are working with a list of reliable suppliers and even if we do
            not know a manufacturer of the products you are looking for, it is
            easy for us to find reliable and suitable ones.
          </p>
          <div
            className="div-more grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="more">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
        <div className="col-md-6 img-col ">
          <img
            className="center-img"
            src={web}
            alt=""
            height="auto"
            width="100%"
          />
        </div>
      </div>

      <div className="row serrow">
        <div className="col-md-6 img-col">
          <img
            className="center-img"
            src={negociation}
            alt=""
            height="auto"
            width="100%"
          />
        </div>
        <div className="col-md-6 text-col">
          <h1
            className="import-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            IMPORT FROM TURKEY
          </h1>
          <p
            className="import-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We assist small business importers find potential suppliers from
            Turkey. We source best products, facilitate negotiation, find
            competitive prices, and take care of documentation. We also follow
            up production and delivery.
          </p>
          <div
            className="div-more grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="more">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>

      <div className="row serrow">
        <div className="col-md-6 text-col">
          <h1
            className="export-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            EXPORT TO AFRICA
          </h1>
          <p
            className="export-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are sourcing agents, facilitating good business between Turkish
            manufacturers and African customers. We help manufacturers find
            potential clients and sell their products in the African market.
          </p>
          <div
            className="div-more grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="more">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
        <div className="col-md-6 img-col">
          <img
            className="center-img"
            src={sale}
            alt=""
            height="auto"
            width="100%"
          />
        </div>
      </div>

      <div className="row serrow">
        <div className="col-md-6 img-col">
          <img
            className="center-img"
            src={digital}
            alt=""
            height="auto"
            width="100%"
          />
        </div>
        <div className="col-md-6 text-col">
          <h1
            className="digital-h1 wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="2s"
          >
            DIGITAL MARKETING
          </h1>
          <p
            className="digital-p wow fadeInUp"
            data-wow-delay="0.4s"
            data-wow-duration="2s"
          >
            We are also digital marketers, using any form of digital channel to
            promote products or services, or to connect with the clients.
          </p>
          <div
            className="div-more grow wow fadeInUp"
            data-wow-delay="0.6s"
            data-wow-duration="2s"
          >
            <a href="https://byg-services.com/#/service">
              <span className="more">
                Learn more <i className="fa fa-long-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Animate;
